.toolbar{
    position: absolute;
    right:70px;
    top:200px;
    border: 2px solid rgb(0, 133, 133);
    border-radius: 5%;
    background-color:#0cc;
    height:35px;
    width: 100px;
    z-index:10;
    list-style: none;
    text-align: center;
}

.toolbar-face-plate{
    position: absolute;
    right:70px;
    top:200px;
    height:35px;
    width:100px;
    &:hover{
        cursor: pointer;
    }
    z-index:11;
    list-style: none;
}

.menu-item{
    list-style: none;
    margin-top:2px;
    margin-bottom:2px;
}

.menu{
    margin-top: 0px;
    margin-bottom: 0px;
    margin-left:auto;
    margin-right: auto;
    padding:0px;
    li{
        opacity: 0;
    }
}

.menu-open{
    @for $i from 0 through 2{
        .menu-item-#{$i}{
            animation: rotateZ 300ms ($i * 100ms) ease-in-out forwards;
        }
    }
}

@keyframes rotateZ{
    0% {
        opacity: 0;
        transform: translateZ(290px);
      }
      
      80% {
        transform: translateZ(10px);
      }
    
      100% {
        opacity: 1;
        transform: translateZ(0);
      }
}

@for $i from 0 through 2{
    .menu-item-#{$i}{
        margin-left: auto;
        margin-right: auto;
        width:90px;
        height:40px;
    }
}



.rotate-up::before{
    animation: rotate90deg .5s linear forwards;
}

.rotate-down::before{
    animation: rotate90deg-down .5s linear forwards;
}

@keyframes rotate90deg{
    50% {transform: rotateZ(90deg);}
    100% { transform: rotateZ(180deg);}
}

@keyframes rotate90deg-down{
    0% {transform: rotateZ(-180deg);}
    50% {transform: rotateZ(-90deg);}
    100% { transform: rotateZ(0deg);}
}

.download-schema{
    position: absolute;
    top: 90%;
    left: 50%;
}