.dialogue{
    margin:0;
    position:absolute;
    top:0;
    left:0;
    width:0px;
    z-index:100;
    height:100%;
    background-color: white;
    animation: slideOut .5s forwards linear;
}

.dialogue-slide-out{
    animation: slideInToView .5s forwards linear;
}

.dialogue-slide-in{
    animation: slideOutOfView .5s forwards linear;
}

@keyframes slideOut{
    50%{width:2%;}
    100%{width:5%;}
}

@keyframes slideInToView{
    0%{width:5%;}
    50%{width: 30%;}
    100%{width: 60%;}
}

@keyframes slideOutOfView{
    0%{width:50%;}
    50%{width: 30%;}
    100%{width: 5%;}
}